.crs_scene {
  position: absolute;
  left: 45vw;
  -webkit-perspective: 800px;
          perspective: 800px;
  margin: auto;
  padding-top: 15vh; }

.crs_body {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform .3s ease-in;
  transition: -webkit-transform .3s ease-in;
  transition: transform .3s ease-in;
  transition: transform .3s ease-in, -webkit-transform .3s ease-in; }

.crs_cell {
  position: absolute;
  width: 100%;
  height: 100%;
  /*
  height: 60px;
  left: 10px;
  top: 10px;
  //line-height: 55px;
  font-size: 36px;
  */
  font-weight: bold;
  box-shadow: 1px 1px 4px 1px black;
  border-radius: 5px;
  background: rgba(220, 220, 220, 0.8);
  background: rgba(var(--clr, 220), var(--clr, 220), var(--clr, 220), var(--opacity, 0.8));
  -webkit-transition: background 1s ease-in, -webkit-transform .2s ease-in;
  transition: background 1s ease-in, -webkit-transform .2s ease-in;
  transition: transform .2s ease-in, background 1s ease-in;
  transition: transform .2s ease-in, background 1s ease-in, -webkit-transform .2s ease-in; }
  .crs_cell:hover {
    box-shadow: 1px 1px 4px 1px black, 0 0 12px 8px #0ff; }

.shadow_ff {
  box-shadow: 1px 1px 4px 1px black, 0 0 8px 4px #ffd700; }

.sun {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  box-shadow: 0 0 6px 0 #fbe602, 0 0 10px 0 #e70f14, 0 0 14px 0 #fabc14;
  -webkit-transition: all .5s ease-in;
  transition: all .5s ease-in; }
  .sun:hover {
    box-shadow: 0 0 26px 32px #fbe602, 0 0 30px 34px #e70f14, 0 0 44px 48px #fabc14; }

.sun-0 {
  -webkit-transform: translate(-10px, -10px);
          transform: translate(-10px, -10px); }

.sun-x90 {
  -webkit-transform: translate(-10px, -10px) rotateX(90deg);
          transform: translate(-10px, -10px) rotateX(90deg); }

.sun-y90 {
  -webkit-transform: translate(-10px, -10px) rotateY(90deg);
          transform: translate(-10px, -10px) rotateY(90deg); }

