.sekki-name, .sekki-name_v {
  text-align: center;
  cursor: pointer;
  -webkit-transition: background .3s ease-in;
  transition: background .3s ease-in;
  text-shadow: .4px .4px 0 gold, -.3px -.3px 0 gray; }

.sekki-name {
  border-radius: 5px 5px 0 0; }

.sekki-name_v {
  float: left;
  -webkit-writing-mode: vertical-lr;
          writing-mode: vertical-lr;
  height: 100%;
  border-radius: 5px 0 0 5px; }

.s72-names, .s72-names_v {
  font-size: 1.6vw;
  line-height: 1.4vw;
  white-space: nowrap;
  cursor: pointer; }
  .s72-names .s72-yomi, .s72-names_v .s72-yomi {
    font-size: .5vw;
    line-height: 1vw;
    color: navy; }
  .s72-names:hover, .s72-names_v:hover {
    text-shadow: 0 0 6px  #fff, 0 0 10px #e60073; }

.s72-names {
  float: left;
  width: 33.33%;
  -webkit-writing-mode: vertical-rl;
          writing-mode: vertical-rl;
  text-align: start;
  padding-top: 6px;
  padding-bottom: 6px; }

.s72-names_v {
  text-align: left;
  height: 33.3%; }

