// uses CSS text-shadow to draw outlines with the color and width in px
// limitations exist, use SVG for better outline when magnified
@mixin textOutline($color, $p: 1px) {
  text-shadow:
    $p $p $color,
    -#{$p} -#{$p} $color,
    $p -#{$p} $color,
    -#{$p} $p $color;
}

