$sun_r : 10px;  // radius of the sun

.sun {
  position: absolute;
  top: 50%;
  left: 50%;
  width:  $sun_r * 2;
  height: $sun_r * 2;
  border-radius: 50%;
  background: white;
  //opacity: .9;
  box-shadow: 0 0 6px 0 #fbe602, 0 0 10px 0 #e70f14, 0 0 14px 0 #fabc14;
  &:hover {
    box-shadow: 0 0 26px 32px #fbe602, 0 0 30px 34px #e70f14, 0 0 44px 48px #fabc14;
  }
  transition: all .5s ease-in;
  //box-shadow: 0px 0px 5px 6px orange;  // offset-x, offset-y, blur-radius, spread-radius
//  animation: sun-glow 1s linear infinite alternate;
//  z-index: 200;
}

@mixin sun-x($deg) { transform: translate(-$sun_r, -$sun_r) rotateX($deg); }
@mixin sun-y($deg) { transform: translate(-$sun_r, -$sun_r) rotateY($deg); }

.sun-0    { transform: translate(-$sun_r, -$sun_r); }  // half the size of width and height
.sun-x90  { @include sun-x(90deg) }
.sun-y90  { @include sun-y(90deg) }
